import cx from 'classnames';
import Link from 'next/link';

import * as React from 'react';

import HeadPost from './HeadPost';

const prd = process.env.NODE_ENV === 'production';

type Props = {
  post: PostInfo;
};

const BlogListItem = ({ post }: Props) => {
  const { link, meta } = post;
  const { draft, temp } = meta;

  if (prd && (draft || temp)) {
    return <></>;
  }

  const url = link.split('/').map(encodeURIComponent).join('/');
  // console.log('BlogListItem', { url, link, temp });
  return (
    <article className={cx('bg-gray-300 p-3 shadow-lg', temp && 'bg-yellow-200')}>
      <Link href={url}>
        <HeadPost meta={meta} />
      </Link>
    </article>
  );
};

export default BlogListItem;
